import React, { useState } from 'react'
import { Avatar, Typography, Grid, Container, Paper, Button } from '@material-ui/core'
import LockOutlinedIcon from '@material-ui/icons/LockOutlined'
import { GoogleLogin } from 'react-google-login'
import { useDispatch } from 'react-redux'
import { useHistory } from 'react-router-dom/cjs/react-router-dom.min'
import { signUp, signIn } from '../../actions/auth'
import { PRIMARY } from '../../constants/themeConstants.js'
import Input from './Input'
import Icon from './icon'
import useStyles from './styles'

const initialState = {
  firstName: '',
  lastName: '',
  email: '',
  password: '',
  confirmPassword: ''
}
export default function Auth () {
  const classes = useStyles()
  const [showPassword, setShowPassword] = useState(false)
  const [isSignUp, setIsSignUp] = useState(false)
  const [formData, setFormData] = useState(initialState)
  const dispatch = useDispatch()
  const history = useHistory()

  const handleSubmit = (e) => {
    e.preventDefault()
    if (isSignUp) {
      dispatch(signUp(formData, history))
    } else {
      dispatch(signIn(formData, history))
    }
  }
  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value })
  }
  const handleShowPassword = () => {
    setShowPassword((prev) => !prev)
  }
  const switchMode = () => {
    setIsSignUp((prev) => !prev)
    setShowPassword(false)
  }
  const googleSuccess = async (res) => {
    const result = res?.profileObj
    const token = res?.tokenId
    try {
      dispatch({ type: 'AUTH', data: { result, token } })
      history.push('/')
    } catch (error) {
      console.log(error)
    }
  }
  const googleFailure = (err) => { console.log(`Google Sign in failed:\n${err}`) }

  return (
    <Container component='main' maxWidth='xs'>
      <Paper className={classes.paper} elevation={3}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography variant='h5'>{isSignUp
          ? 'Sign Up' : 'Sign In'}
        </Typography>
        <form className={classes.form} onSubmit={handleSubmit}>
          <Grid container spacing={2}>
            {
                            isSignUp && (
                              <>
                                <Input
                                  name='firstName'
                                  label='First Name'
                                  handleChange={handleChange}
                                  autoFocus half
                                />
                                <Input
                                  name='lastName'
                                  label='Last Name'
                                  handleChange={handleChange}
                                  half
                                />
                              </>

                            )
                        }
            <Input
              name='email'
              label='Email Address'
              handleChange={handleChange}
              type='email'
            />
            <Input
              name='password'
              label='Password'
              handleChange={handleChange}
              type={showPassword ? 'text' : 'password'}
              handleShowPassword={handleShowPassword}
            />
            {isSignUp && <Input
              name='confirmPassword'
              label='Repeat Password'
              handleChange={handleChange}
              type='password'
                         />}
          </Grid>
          <Button type='submit' fullWidth variant='contained' color='primary' className={classes.submit}>
            {isSignUp ? 'Sign Up' : 'Sign In'}
          </Button>
          <GoogleLogin
            clientId='401269994258-524s8igilpeebjga0ltfrt49anv8e118.apps.googleusercontent.com'
            render={(renderProps) => (
              <Button
                fullWidth
                className={classes.googleButton}
                color='primary'
                onClick={renderProps.onClick}
                disabled={renderProps.disabled}
                startIcon={<Icon />}
                variant='contained'
              >
                Google Sign In
              </Button>
            )}
            onSuccess={googleSuccess}
            onFailure={googleFailure}
            cookiePolicy='single_host_origin'
          />
          <Grid container justifyContent='flex-end'>
            <Button style={{ fontSize: '.8rem', fontWeight: '400', color: PRIMARY }} onClick={switchMode}>
              {isSignUp
                ? 'Already have an account? Sign In'
                : 'Don\'t have an account? Sign Up'}
            </Button>
          </Grid>
        </form>
      </Paper>

    </Container>
  )
}
